* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: flex;
}

.card__description {
  text-align: left;
  margin-bottom: 16px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__review-title {
  text-align: right;
  justify-content: flex-end;
  align-items: center;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__text {
  color: #616070;
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  text-align: center;
  content: "";
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}
